'use strict';

define(['text!./view.html', 'app/config', 'knockout'], function (template, config) {

    function ViewModel() {

        this.year = new Date().getFullYear();
        this.email = config.contact;
        this.emailLink = 'mailto:' + this.email;
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});